import request from '@/utils/request'
// 上传文件
export function uploadFile (data) {
  return request({
    url: '/standard/upload',
    method: 'post',
    data
    // headers:{'Content-Type': 'multipart/form-data' }
  })
}

// 全部文件
export function getAllFiles () {
  return request({
    url: '/standard/getfiles',
    method: 'get'
    // headers:{'Content-Type': 'multipart/form-data' }
  })
}

// '/standard/openfile?id=?'
export function downloadFile (id) {
  return request({
    url: `/standard/openfile?id=${id}`,
    method: 'get'
    // headers:{'Content-Type': 'multipart/form-data' }
  })
}
// 删除删除文件  '/standard/delete?id=?'
export function deleteFile (id) {
  return request({
    url: `/standard/delete?id=${id}`,
    method: 'get'
    // headers:{'Content-Type': 'multipart/form-data' }
  })
}
// 文件  '/standard/delete?id=?'
export function searchFile (keyword) {
  return request({
    url: `/standard/findfile?keyword=${keyword}`,
    method: 'get'
    // headers:{'Content-Type': 'multipart/form-data' }
  })
}
