<template>
  <div class="material">
    <div class="content">
      <el-tabs type="card">
        <el-tab-pane>
          <span slot="label"><i class="el-icon-upload"></i> 标准规范文件</span>
          <el-row>
            <el-col :span="12">
              <el-input
                placeholder="请输入内容"
                v-model="searchValue"
                style="width: 60%"
              >
                <el-button
                  slot="append"
                  icon="el-icon-search"
                  @click="handleSearchFile"
                ></el-button>
              </el-input>
              <el-button type="primary" class="ml-10" @click="handleReset"
                >重置</el-button
              >
            </el-col>
            <el-col :span="8" :offset="4" style="text-align: right">
              <el-upload
                class="upload-demo"
                ref="upload"
                action="no"
                multiple
                :http-request="uploadFileConfig"
                :before-upload="handleBeforepload"
                :on-success="handleSuccess"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                :auto-upload="false"
              >
                <el-button slot="trigger" type="primary">选取文件</el-button>
                <el-button
                  style="margin-left: 10px"
                  type="success"
                  @click="submitUpload"
                  >上传到服务器</el-button
                >
              </el-upload>
            </el-col>
          </el-row>

          <hr />
          <el-table
            v-loading="loading"
            ref="multipleTable"
            highlight-current-row
            :data="currentTableData"
            :header-cell-style="{ 'text-align': 'center' }"
            tooltip-effect="dark"
            style="width: 100%; margin-top: 20px"
            max-height="650"
            border
          >
            <el-table-column
              type="index"
              width="50"
              label="序号"
              align="center"
            />
            <el-table-column
              prop="file_name"
              label="文件名称"
              align="center"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column label="操作" width="350" align="center">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handelDownload(scope.row.id)"
                  >导出</el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  @click="handelDelete(scope.row.id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            v-if="currentTableData.length"
            background
            :page-size="10"
            style="float: right; margin-top: 30px"
            :current-page.sync="currentPage"
            @current-change="handleCurrentChange"
            layout="total, prev, pager, next, jumper"
            :total="tableData.length"
          >
          </el-pagination>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import {
  uploadFile, getAllFiles, downloadFile, deleteFile, searchFile
} from '@/api/standard'
import { parseTime } from '@/utils/index'
export default {
  data () {
    return {
      tableData: [],
      currentTableData: [],
      currentPage: 1,
      searchValue: '',
      loading: false,
      fileList: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    async init () {
      this.loading = true
      try {
        const data = await getAllFiles()
        this.tableData = data.result
        this.currentTableData = data.result.slice(0, 10)
        this.loading = false
      } catch (error) {
        this.loading = false
        return error
      }
    },
    // 上传到服务器
    submitUpload () {
      this.$refs.upload.submit()
    },
    async uploadFileConfig (val) {
      const fd = new FormData()
      fd.append('uploader', localStorage.getItem('name'))
      fd.append('uptime', parseTime(new Date()))
      fd.append('file', val.file)
      fd.append('name', val.name)
      await uploadFile(fd)
      this.init()
      this.$message.success(val.file.name + ' 上传成功')
    },
    handleBeforepload (file) {
      console.log(file)
    },
    handleSuccess (response, file, fileList) {
      const i = fileList.findIndex((val) => val === file)
      fileList.splice(i, 1)
    },
    // 删除文件
    async handelDelete (id) {
      const confirmRlust = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmRlust === 'confirm') {
        try {
          await deleteFile(id)
          this.init()
          this.$message.success('删除成功')
          this.loading = false
        } catch (error) {
          this.loading = false
          return error
        }
      } else {
        this.$message.info('取消了删除!')
      }
      console.log('delete')
    },
    async handelDownload (id) {
      try {
        const data = await downloadFile(id)
        // window.open("/api" + res);
        // window.location.href = "/api" + data;
        window.open(this.$url + data)
      } catch (error) {
        return error
      }
    },
    async handleSearchFile () {
      const res = await searchFile(this.searchValue)
      this.tableData = res.result
      this.currentTableData = res.result.slice(0, 10)
      this.currentPage = 1
    },
    handleReset () {
      this.searchValue = ''
      this.init()
      this.currentPage = 1
    },

    handleRemove (file, fileList) {
      // console.log(file, fileList)
    },
    handlePreview (file) {
      // console.log(file)
    },
    handleCurrentChange () {
      this.currentTableData = this.tableData.slice(
        (this.currentPage - 1) * 10,
        this.currentPage * 10
      )
    }
  }
}
</script>
<style lang="less" scoped>
.material {
  width: 95%;
  padding: 0 20px 20px 20px;
  margin: 0 auto;
}
</style>
